.drag-box {
  position: absolute;
  border-collapse: collapse;
  border: none;
  background: transparent;
}

.button_rotate {
  background: transparent;
  border: none;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  height: 0.5rem;
  width: 0.5rem;
  display: none;
  background: url(../../../../../images/rotate.png) no-repeat;
  background-size: 0.5rem 0.5rem;
}
.button_flip {
  background: transparent;
  border: none;
  position: absolute;
  top: 0.25rem;
  right: 0.25rem;
  height: 0.5rem;
  width: 0.5rem;
  display: none;
  background: url(../../../../../images/flip.png) no-repeat;
  background-size: 0.5rem 0.5rem;
}

.drag-row {
  height: 1rem;
}
.grid-item-empty {
  height: 1rem;
  width: 1rem;
  display: inline-block;
}

.grid-item-active {
  height: 1rem;
  width: 1rem;
  display: inline-block;
}

/* 添加拖动时的样式 */
.dragging {
  cursor: grabbing !important;
}
