.board {
  position: absolute;
  /* background-color: pink; */
  height: 5rem;
  width: 10rem;
  left: calc(50%);
  margin-left: -5rem;
  display: grid;
  grid-template-columns: repeat(10, 1rem);
  grid-template-rows: repeat(5, 1rem);
  border-collapse: collapse;
  cursor: move;
  border: 1px solid rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  background: transparent;
}

.grid-item-normal {
  width: 1rem;
  height: 1rem;
  line-height: 1rem;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.05);
  text-align: center;
  font-size: 0.3rem;
  font-weight: 400;
  border: 1px solid rgba(0, 0, 0, 0.1);
  cursor: move;
  color: darkgoldenrod;
}

.grid-item-today {
  width: 1rem;
  height: 1rem;
  line-height: 1rem;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.05);
  text-align: center;
  font-size: 0.3rem;
  border: 1px solid rgba(0, 0, 0, 0.1);
  cursor: move;
  background-color: skyblue;
  color: darkgoldenrod;
  background: url(../../../../../images/star.png) no-repeat;
  background-size: 1rem 1rem;
}
